<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="140px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="USDT(ERC20) 地址">
              <el-input v-model="setting.usdt_erc20_addr" size="small" placeholder="USDT(ERC20) 地址"> </el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="ERC20二维码">
              <el-upload
                :limit="1"
                :headers="myHeaders"
                :action="'/v1/api/api/api/upload'"
                :on-success="fileSuccess"
                :before-upload="fileSuccessBefore"
                :file-list="file"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽32px 高:84px 格式:png、jpg、jpeg、gif
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="USDT(TRC20) 地址">
              <el-input v-model="setting.usdt_trc20_addr" size="small" placeholder="USDT(TRC20) 地址"> </el-input>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="TRC20二维码">
              <el-upload
                :limit="1"
                :headers="myHeaders"
                :action="'/v1/api/api/api/upload'"
                :on-success="fileSuccess1"
                :before-upload="fileSuccess1Before"
                :file-list="file_user"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽150px 高:150px 格式:png、jpg、jpeg、gif
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="开关">
              <el-select size="small" v-model="setting.store_switch" clearable placeholder="请选择">
                <el-option value="on" label="开" key="开" />
                <el-option value="off" label="关" key="关" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <el-col>
        <el-form-item>
          <el-button type="primary" size="small" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      setting: {
        usdt_erc20_addr: '',
        usdt_trc20_addr: '',
      },
      file: [],
      file_user: [],
      myHeaders: { Authorization: 'Bearer ' + localStorage.getItem('pstt') },
    }
  },
  mounted() {
    this.loading = true
    Fetch2('/sys/config/usdtsetting', {
      headers: { Authorization: `Bearer ` + this.$store.state.user.data.pstt },
    })
      .then(r => {
        this.setting = r.data
        this.loading = false

        if (r.data.logo) {
          this.file = [
            {
              url: r.data.logo,
              name: r.data.logo,
            },
          ]
        }
        if (r.data.user_logo) {
          this.file_user = [
            {
              url: r.data.user_logo,
              name: r.data.user_logo,
            },
          ]
        }
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    onSubmit() {
      this.loading = true
      Fetch3('/sys/config/usdtsetting', {
        ...this.setting,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
    fileSuccess(res, file) {
      this.file = [file]
      this.setting.usdt_erc_qr = res.data.file
    },
    fileSuccess1(res, file) {
      debugger
      this.file_user = [file]
      console.log(res.data.file)
      this.setting.usdt_trc_qr = res.data.file
    },
    fileSuccess1Before(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },
  },
}
</script>
